import { NgModule } from '@angular/core'
import { CommonModule } from '@angular/common'

import { GaRoutingModule } from './ga-routing.module'
import { NzTableModule, NzLayoutModule } from 'ng-zorro-antd'
import { ApiModule } from 'src/app/sdk/hr/api.module'
import { PerfectScrollbarModule } from 'ngx-perfect-scrollbar'
import { WidgetsComponentsModule } from 'src/app/components/widgets/widgets-components.module'
import { LayoutModule } from 'src/app/components/layout/layout.module'
import { ReactiveFormsModule, FormsModule } from '@angular/forms'
import { HTTP_INTERCEPTORS } from '@angular/common/http'
import { JwtInterceptor } from 'src/app/_role/jwt.interceptor'
import { ErrorInterceptor } from 'src/app/_role/error.interceptor'
import { PipeModule } from 'src/app/pipe/pipe.module'

@NgModule({
    declarations: [],
    imports: [
        CommonModule,
        GaRoutingModule,
        NzTableModule,
        ApiModule.forRoot({ rootUrl: 'http://erp-hr.vm.devetek.com/api' }),
        PerfectScrollbarModule,
        WidgetsComponentsModule,
        NzLayoutModule,
        LayoutModule,
        ReactiveFormsModule,
        FormsModule,
        PipeModule
    ],
    providers: [
        { provide: HTTP_INTERCEPTORS, useClass: JwtInterceptor, multi: true },
        { provide: HTTP_INTERCEPTORS, useClass: ErrorInterceptor, multi: true },
    ],
})
export class GaModule { }
